import { Component } from '@angular/core';
import { DxDateBoxModule } from 'devextreme-angular/ui/date-box';

import {
  forwardRef,
  OnInit,
  Output,
  ViewEncapsulation,
  ViewChild,
  EventEmitter,
  ChangeDetectorRef,
  ChangeDetectionStrategy,
} from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';

import { CommonModule, DatePipe } from '@angular/common';
import { KeyDownEvent, ValueChangedEvent } from 'devextreme/ui/date_box';

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => WaproDateBoxComponent),
  multi: true,
};

import { locale, loadMessages, formatMessage } from 'devextreme/localization';
import enMessages from '../../assets/devextreme/localization/messages/en.json';
import plMessages from '../../assets/devextreme/localization/messages/pl.json';

@Component({
  selector: 'wapro-date-box',
  templateUrl: './wapro-date-box.component.html',
  styleUrls: ['./wapro-date-box.component.scss'],
  inputs: [
    'displayFormat',
    'readOnly',
    'type',
    'disabled',
    'errorClass',
    'width',
    'noMaxWidth',
  ],
  standalone: true,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR],
  imports: [CommonModule, DxDateBoxModule],
})
export class WaproDateBoxComponent implements OnInit, ControlValueAccessor {
  @Output() onValueChanged = new EventEmitter();
  @ViewChild('dateBox') dateBox;

  @Output() focusedOut = new EventEmitter();
  @Output() focusedIn = new EventEmitter();
  locale: string = 'pl';
  noMaxWidth: boolean = false;
  width: number | string | any = null;
  private _value: string | number | Date | any;
  displayFormat: string = 'yyyy-MM-dd';
  readOnly = false;
  type: 'date' | 'datetime' | 'time' = 'date';
  disabled = false;
  errorClass = false;

  isHover: boolean = false;
  isFocus: boolean = false;
  plusBtn = {
    icon: 'icon absui-icon--add',
    stylingMode: 'text',
    onClick: () => {
      this.addDay();
    },
  };
  minusBtn = {
    icon: 'icon absui-icon--remove',
    stylingMode: 'text',
    onClick: () => {
      this.subtractDay();
    },
  };
  opened: boolean = false;
  notOpen: boolean = false;
  calendarBtn = {
    icon: 'icon absui-icon--calendar-month-unselect',
    stylingMode: 'text',
    tabIndex: -1,
    onClick: () => {
      this.opened = !this.opened;
      this.cd.detectChanges();
      if (this.opened) this.dateBox.instance.focus();
    },
  };
  calendarOptions: any;
  changeByUser: boolean = false;

  public get myValue(): string | number | Date | any {
    return this._value;
  }
  public set myValue(v: string | number | Date | any) {
    if (v !== this._value) {
      this._value = v;
      try {
        this.onChange(v);
      } catch {}
    }
  }

  constructor(public cd: ChangeDetectorRef) {
    if (!this.displayFormat) this.displayFormat = 'yyyy-MM-dd';
    if (!this.type) this.type = 'date';
    if (!this.disabled) this.disabled = false;

    this.calendarOptions = {
      onContentReady: (e) => {
        setTimeout(function () {
          e.component.option('inputAttr', { readonly: true });
        }, 500);
        let todayBtn =
          this.dateBox.element.nativeElement.getElementsByClassName(
            'dx-button-today'
          )[0] as HTMLElement;
        if (this.type != 'datetime') {
          let buttonDone = document.getElementsByClassName(
            'dx-widget dx-button dx-button-mode-contained dx-button-normal dx-button-has-text dx-popup-done'
          );
          let buttonCancel = document.getElementsByClassName(
            'dx-widget dx-button dx-button-mode-contained dx-button-normal dx-button-has-text dx-popup-cancel'
          );

          for (let i = 0; i < buttonDone.length; i++) {
            buttonDone[i].classList.add('disabledBtn');
          }

          for (let i = 0; i < buttonCancel.length; i++) {
            buttonCancel[i].classList.add('disabledBtn');
          }

          e.element.onclick = () => {
            let newDate = new DatePipe('en-US').transform(
              e.component.option('value'),
              'yyyy-MM-ddTHH:mm:ss'
            );
            this.changeByUser = true;
            this.myValue = newDate;
            this.cd.detectChanges();
          };
        }

        todayBtn.onclick = () => {
          let newDate = new DatePipe('en-US').transform(
            new Date(),
            'yyyy-MM-ddTHH:mm:ss'
          );
          this.changeByUser = true;
          this.myValue = newDate;
          this.opened = false;
          this.cd.detectChanges();
        };
      },
    };
  }

  ngOnInit(): void {
    this.initMessages();
    this.locale = this.getLocale();
    locale(this.locale);
  }

  initMessages() {
    loadMessages(plMessages);
    loadMessages(enMessages);
  }

  getLocale() {
    var locale;
    if (typeof window !== 'undefined') {
      locale = window.localStorage.getItem('lang');
      return locale != null ? locale : 'pl';
    }
  }

  addDay = () => {
    if (
      this.myValue == null ||
      this.myValue == undefined ||
      this.myValue == ''
    ) {
      this.myValue = new DatePipe('en-US').transform(
        new Date(),
        'yyyy-MM-ddTHH:mm:ss'
      );
    }

    let newDate = new DatePipe('en-US').transform(
      new Date(this.myValue).getTime() + 1 * 1000 * 60 * 60 * 24,
      'yyyy-MM-ddTHH:mm:ss'
    );
    this.changeByUser = true;
    this.myValue = newDate;
    this.cd.detectChanges();
  };

  openBox = () => {
    if (!this.notOpen) {
      this.opened = !this.opened;
    }
  };

  onOpened = () => {
    this.notOpen = true;
  };

  onClosed = () => {
    setTimeout(() => {
      this.notOpen = false;
    }, 500);
  };

  onValueChangedDate = (e: ValueChangedEvent) => {
    if (this.type != 'datetime') {
      this.opened = false;
      this.cd.detectChanges();
    }
    const event = { ...e };
    if (!event.event) {
      event.event = {} as any;
    }
    this.onValueChanged.emit(event);
    this.changeByUser = false;
  };

  onBlur = (e) => {
    e.element.classList.remove('dx-state-focused');
  };

  subtractDay = () => {
    if (
      this.myValue == null ||
      this.myValue == undefined ||
      this.myValue == ''
    ) {
      this.myValue = new DatePipe('en-US').transform(
        new Date(),
        'yyyy-MM-ddTHH:mm:ss'
      );
    }

    let newDate = new DatePipe('en-US').transform(
      new Date(this.myValue).getTime() - 1 * 1000 * 60 * 60 * 24,
      'yyyy-MM-ddTHH:mm:ss'
    );
    this.changeByUser = true;
    this.myValue = newDate;
    this.cd.detectChanges();
  };

  keydown = (e: KeyDownEvent) => {
    if (!this.readOnly) {
      const originalEvent = (e.event as any).originalEvent;
      if (originalEvent.code == 'ArrowDown' && originalEvent.shiftKey)
        this.subtractDay();
      if (originalEvent.code == 'ArrowUp' && originalEvent.shiftKey)
        this.addDay();
      this.cd.detectChanges();
    }
  };

  mouseover = () => {
    this.isHover = true;
  };

  mouseout = () => {
    this.isHover = false;
  };

  onFocusIn = () => {
    this.isFocus = true;
    this.focusedIn.emit(true);
  };

  onFocusOut = () => {
    this.isFocus = false;
    this.focusedOut.emit(true);
  };

  onChange = (_) => {};
  onTouched = () => {};

  writeValue(value: any): void {
    this.myValue = value;
    this.cd.detectChanges();
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
    this.cd.detectChanges();
  }
  registerOnTouched(fn: any): void {
    this.onTouched = fn;
    this.cd.detectChanges();
  }
  setDisabledState?(_isDisabled: boolean): void {
    //throw new Error('Method not implemented.');
  }
}
